
h1, h2 {
  font-weight: normal;
}
.home {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  min-height: 80vh;
  min-width: 40vw;
  background-color: #ECECEC;
  height: 200px;
}
.text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: left;
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 20px;
  width: 50vw;
  padding: 15px;
  margin-right: 10px;
}
.text h1 {
  margin-left: 3%;
}
.text p {
  padding: 20px;
  text-align: justify;
  font-size: 20px;
  line-height: 2em;
}
.form {
  margin-right: 40px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 0;
  padding: 40px;
  width: 40vw;
  background: #FFF;
  height: 80%;
  overflow-x: hidden;
  overflow-y: scroll;
  /*box-shadow: 5px 5px 5px 5px grey;*/
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
@media only screen
and (min-device-width : 768px)
and (max-device-width : 1024px)
and (orientation : landscape) {
.form {
    max-height: 47vh;
    margin-top: 100px;
}
.text {
    margin-top: 100px;
}
}
@media only screen
and (min-device-width : 320px)
and (max-device-width : 568px)
and (orientation: portrait) {
.text {
    display: none;
}
.form {
    width: 100%;
    height: 80%;
    margin-left: 40px;
}
}




