
.main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 80vh;
  min-width: 40vw;
  background-color: #ECECEC;
  height: 300px;
}
.dash {
  display: -ms-grid;
  display: grid;
  margin: 0 0 0 110px;
  grid-gap: 50px;
  -ms-grid-columns: 1fr 15% 150px 1fr;
      grid-template-columns: 1fr 15% 150px 1fr;
  -ms-grid-rows: 250px 250px;
      grid-template-rows: 250px 250px;
  grid-template-areas: "a b" "c c";
}
.a {
  grid-columns: a;
}
.b {
  grid-columns: b;
}
.c {
  grid-area: c;
}
.dash-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /*justify-content: center;*/
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #FAFAFA;
}
.form-one {
  margin-top: 30px;
  width: 40vw;
  background: #FFF;
  overflow-x: hidden;
  /*box-shadow: 5px 5px 5px 5px grey;*/
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.form-two {
  margin-top: 30px;
  width: 40vw;
  background: #FFF;
  overflow-x: hidden;
  /*box-shadow: 5px 5px 5px 5px grey;*/
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.table-class {
  background: #FFF;
  width: 83.5vw;
  height: 200px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.form-save-class {
  width: 75%;
}
.form-save-student {
  width: 450px;
  margin: auto auto auto 50px;
  padding: 20px;
}
.md-raised {
  margin: 20px auto auto auto;
}
.class-title {
  padding-top: 5%;
  color: #1a237e;
}



