
html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
}
body {
    background-color: #FAFAFA;
    min-height: 100vh;
    max-width: 100%;
}
body p {
  font-family: 'Roboto', sans-serif;
}
body h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto', sans-serif;
}
ul {
  list-style: none;
}
.header {
    height: 10vh;
    width: 100%;
    color: #006;
    border-bottom: 1px solid #006;
    overflow: hidden;
}
.header img {
    position: relative;
    width: 10%;
    overflow: hidden;
    left: 45%;
    top: 20%;
}
.footer {
    height: 10vh;
    width: 100%;
    color: #b8b8b8;
    border-top: 1px solid #006;
    background-color: #006;
    margin: 0;
}
.footer p {
    position: relative;
    margin: 20px;
    left: 10%;
    bottom: 0%;
    font-size: 15px;
    color: #FFF;
}
.label-error {
  color: red;
  display: block;
}
@media only screen
and (min-device-width : 320px)
and (max-device-width : 568px)
and (orientation: portrait) {
.header img {
    width: 40%;
    left: 30%;
}
.footer p {
    font-size: 20px;
    left: 26%;
}
.md-tabs .md-tabs-navigation-container {
    overflow-x: hidden;
}
.md-tabs .md-tabs-navigation.md-has-navigation-scroll .md-tab-header-navigation-button.md-left {
    display: none;
}
.md-tabs .md-tabs-navigation.md-has-navigation-scroll .md-tab-header-navigation-button.md-right {
    display: none;
}
.md-input-container label {
    font-size: 13px;
}
}



