
.main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 80vh;
  min-width: 40vw;
  background-color: #ECECEC;
  height: 200px;
}
.list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /*align-items: center;*/
  text-decoration: none;
  border-right: 1px solid #000;
  border-radius: 2px;
}
.dash {
  display: -ms-grid;
  display: grid;
  width: 100%;
  height: 100%;
  margin: 20px;
  grid-gap: 20px;
  -ms-grid-columns: auto;
      grid-template-columns: auto;
  -ms-grid-rows: 220px 220px;
      grid-template-rows: 220px 220px;
  grid-template-areas:
    'a b c '
    'd e f '
  ;
}
.a {
  grid-area: a;
}
.b {
  grid-area: b;
}
.c {
  grid-area: c;
}
.d {
  grid-area: d;
}
.e {
  grid-area: e;
}
.f {
  grid-area: f;
}
.dash-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  /*justify-content: center;*/
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #FAFAFA;
}
hr {
  color: #000;
  width: 100%;
}
.list span{
  color:#FFF;
}
.list a {
  color: #FFF;
}
.dash-item span {
  /*text-decoration: none;*/
  /*color: #FF8F00;*/
  color: #006;
  margin: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.logout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  left: 90vw;
  top: 2vh;
  color: #006;
}
.logout i {
  font-size: 5vh;
}



