
.vform span {
  color: #F00;
}
@media only screen
and (min-device-width : 320px)
and (max-device-width : 568px)
and (orientation: portrait) {
.vform {
    width: 100%;
    min-width: 40vw;
}
.vform md-input-container {
    width: 100%;
    min-width: 40vw;
}
}




